.featured-clothes-wrapper {
	display: block;
	width: 100%;
	overflow: auto;

	> * {
		float: left;
		width: 310px;
		margin-bottom: 60px;
		position: relative;

		&:nth-child(3n+1) {
			// background-color: gold;
		}

		&:nth-of-type(3n+2) {
			// background-color: pink;
			margin-left: 25px;
			margin-right: 25px;
		}

		&:nth-of-type(3n+3) {
			// background-color: lightblue;
		}

		&:nth-last-child(-n+3) {
			margin-bottom: 0;
		}

		img {
			width: 100%;
		}

		.price, .size {
			font-weight: 500;
			position: absolute;
			top: 10px;
			padding: 10px;
			background-color: $cLightTransparant;
		}

		.price {
			left: 10px;

			span {
				color: gray;
				font-size: .9em;
			}
		}

		.size {
			right: 10px;
		}

		h1 {
			margin: 10px 0;
		}

		.colors {
			div {
				width: 15px;
				height: 15px;
				margin-right: 4px;
				border-radius: 50%;
				float: left;
			}
		}
	}
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
	.featured-clothes-wrapper {
		> * {
			width: 240px;
			margin-bottom: 50px;

			&:nth-of-type(3n+2) {
				margin-left: 20px;
				margin-right: 20px;
			}

			.price, .size {
				top: 7px;
				padding: 7px;
			}

			.price {
				left: 7px;
			}

			.size {
				right: 7px;
			}

			h1 {
				margin: 7px 0;
			}
		}
	}
}

/* Small Devices, Tablets */
@media only screen and (max-width : 768px) {
	.featured-clothes-wrapper {
		> * {
			width: 225px;
			margin-bottom: 40px;

			&:nth-of-type(3n+2) {
				margin-left: 0;
				margin-right: 0;
			}

			&:nth-of-type(2n+2) {
				margin-left: 20px;
			}

			&:nth-last-child(-n+3) {
				margin-bottom: 40px;
			}

			&:nth-last-child(-n+2) {
				margin-bottom: 0;
			}

			h1 {
				margin: 5px 0;
			}
		}
	}
}

/* Extra Small Devices, Phones */ 
@media only screen and (max-width : 480px) {
	.featured-clothes-wrapper {
		> * {
			width: 100%;
			margin: 0 0 35px 0 !important;

			&:last-child {
				margin-bottom: 0 !important;
			}
		}
	}
}

/* Custom, iPhone Retina */ 
@media only screen and (max-width : 320px) {
	.featured-clothes-wrapper {
		> * {
			.price, .size {
				top: 5px;
				padding: 5px;
			}

			.price {
				left: 6px;
			}

			.size {
				right: 6px;
			}

			h1 {
				margin: 3px 0;
			}
		}
    }
}